.page {
  padding-top: 60px;
}
@media screen and (min-width: 768px) {
  .page {
    display: flex;
    padding-top: 90px;
  }
}
@media screen and (min-width: 1280px) {
  .page {
    display: flex;
    padding-top: 103px;
  }
}

.h1 {
  font-family: 'Manrope';
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 44px;
  color: black;
  padding-left: 20px;
  -bottom: 58px;
}
@media screen and (min-width: 768px) {
  .h1 {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 700;
    font-size: 68px;
    line-height: 100px;
    padding-left: 20px;
  }
}

.container {
  margin: 0;
  padding: 0;
}

