.imageHeart {
  position: absolute;
  right: 460 px;
  bottom: 509 px;
  object-fit: cover;
}
@media screen and (max-width: 1279px) {
  .imageHeart {
    display: none;
  }
}

.frame {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: -15;
}
@media screen and (max-width: 1280px) {
  .frame {
    display: none;
  }
}
/* export const ImageWrapper = styled.div` */

.imageDog {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: -10;
}
@media screen and (max-width: 767px) {
  .imageDog {
    max-width: 100%;
    right: 50%;
    transform: translateX(50%);
  }
}
@media screen and (min-width: 767px) and (max-width: 1279px) {
  .imageDog {
    max-width: 100%;

    right: 50%;
    transform: translateX(50%);
  }
}

.imageBG {
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  z-index: -20;
}
@media screen and (max-width: 759px) {
  .imageBG {
    max-height: 100%;
    min-height: 500px;
    bottom: 0px;
  }
}
@media screen and (min-width: 761px) and (max-width: 1280px) {
  .imageBG {
    bottom: 0;

    max-height: 1080px;
  }
}
